import svg4everybody from 'svg4everybody';
import $ from 'jquery';
import 'fullpage.js';
import {TimelineMax, Elastic} from 'gsap';
import '../blocks/fullpage/fullpage.js';
import '../blocks/menu/menu.js';

$(() => {
	svg4everybody();
});


var intro = $('.header__intro');
var media = $('.header__media');
var reads = $('.reads__content .reads__wrap');
var block1 = $('.present-gallery__part_js-1');
var block2 = $('.present-gallery__part_js-2');
var block3 = $('.present-gallery__part_js-3');
var block4 = $('.present-gallery__part_js-4');
var feedback = $('.feedback');

let tl = new TimelineMax

tl	
	.fromTo('.logo', 0.5, {y:-200,opacity: 0}, {y: 0,opacity: 1})
	.fromTo('.burger', 1, {y:-200,opacity: 0}, {y: 0,opacity: 1})
	.staggerFromTo('.navigation__panel .navigation__elem', 0.8, {opacity:0,x: 30}, {opacity: 1,x: 0}, .5)
	.staggerFromTo('.reads__content .reads__wrap', 3, {opacity:0,rotation: 500}, {opacity: 1,rotation: 0, scale: 1,  ease: Bounce.easeOut});



		



